import axios from 'axios';
import {
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  USER_LOADED,
  AUTH_ERROR,
  LOGIN_FAIL,
  LOGIN_SUCCESS,
  LOGOUT,
  CLEAR_PROFILE,
  RegisterUser,
  Login,
} from './constants';
import { setAlert } from './alert';
import setAuthToken from '../utils/setAuthToken';

//Thunk dispatch stuff
import { Action } from 'redux';
import { AppState } from '../reducers/index';
import { ThunkAction } from 'redux-thunk';

//Load User
export const loadUser = (): ThunkAction<
  void,
  AppState,
  unknown,
  Action<string>
> => async (dispatch) => {
  if (localStorage.token) {
    setAuthToken(localStorage.token);
  }
  try {
    const res = await axios.get('/api/auth');
    dispatch({
      type: USER_LOADED,
      payload: res.data,
    });
  } catch (error) {
    console.log(error);
    dispatch({
      type: AUTH_ERROR,
    });
  }
};

//Register User
export const register = ({
  name,
  email,
  password,
}: RegisterUser): ThunkAction<
  void,
  AppState,
  unknown,
  Action<string>
> => async (dispatch) => {
  const config = {
    headers: {
      'Content-Type': 'application/json',
    },
  };
  const body = JSON.stringify({ name, email, password });
  try {
    const res = await axios.post('/api/users', body, config);
    dispatch({
      type: REGISTER_SUCCESS,
      payload: res.data,
    });
    dispatch(loadUser());
  } catch (error) {
    const errors = error.response.data.errors;
    if (errors) {
      errors.forEach((error: NodeJS.ErrnoException) =>
        dispatch(setAlert(error.code, 'danger'))
      );
    }
    dispatch({
      type: REGISTER_FAIL,
    });
  }
};

//Login User
export const login = ({
  email,
  password,
}: Login): ThunkAction<void, AppState, unknown, Action<string>> => async (
  dispatch
) => {
  const config = {
    headers: {
      'Content-Type': 'application/json',
    },
  };
  const body = JSON.stringify({ email, password });
  try {
    const res = await axios.post('/api/auth', body, config);
    dispatch({
      type: LOGIN_SUCCESS,
      payload: res.data,
    });
    dispatch(loadUser());
  } catch (error) {
    if (error.response) {
      console.log(error);
      const errors = error.response.data.errors;
      if (errors) {
        errors.forEach((error: NodeJS.ErrnoException) =>
          dispatch(setAlert(error.code, 'danger'))
        );
      }
    }
    dispatch({
      type: LOGIN_FAIL,
    });
  }
};

//Logout User /clear profile
export const logout = (): ThunkAction<
  void,
  AppState,
  unknown,
  Action<string>
> => (dispatch) => {
  dispatch({
    type: CLEAR_PROFILE,
  });
  dispatch({
    type: LOGOUT,
  });
};
